body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.brand_logo{
    max-width: 200px;
    max-height: auto;
}

.login-form {
    /* display: inline-block; */
    float: left;
}

.google-login {
    /* display: inline-block; */
    float: right;
}

.center-div {
    text-align: center;
}
.footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height:30px;
    background-color: rgb(196, 133, 17);
    color: white;
    font-size: 13px;
    font-weight: bold;
    text-align: center;
    padding: 5px;
}



.bg-light {
    background-color: #F2B544!important;
    border-color: rgb(197, 146, 52);
    border-width: 5px;
    border-style: ridge;
}





